import { React, useEffect, useState } from "react";

import ReactPaginate from 'react-paginate';

import Masonry from 'react-masonry-css'

import { Modal, Button, Form } from 'react-bootstrap';

let displayElements;

function cellRenderer({columnIndex, key, rowIndex, style}) {
  return (
    <div key={key} style={style}>
      {displayElements[columnIndex + rowIndex * 3]}
    </div>
  );
}

function PageSelection(props){
  const[pageNumber, setPageNumber] = useState(0);

  const handleClose = props.handleClose;
  const setPage = props.setPage;
  const show = props.show;
  return(
    <Modal show={show} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Jump to page</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Control type="number" onChange={(e) => setPageNumber(Number(e.target.value))}/>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Annuller
          </Button>
          <Button variant="primary" onClick={() => {
            if(pageNumber > 0)
              setPage(pageNumber-1);
            handleClose();
          }}>
            OK
          </Button>
        </Modal.Footer>
    </Modal>
  );
}

export default function PaginationContainer(props) {
    const[pageNumber, setPageNumber] = useState(0);
    const[showPageSelection, setShowPageSelection] = useState(false);
    const elements = props.elements;

    const elementsPerPage = 10;
    const pagesVisited = pageNumber * elementsPerPage;

    displayElements = elements.slice(pagesVisited, pagesVisited + elementsPerPage);

    const changePage = ({selected}) => {
      setPageNumber(selected);
    }

    const openPageSelection = () => setShowPageSelection(true);
    const closePageSelection = () => setShowPageSelection(false);

    return (
      <div>
        <PageSelection show={showPageSelection} setPage={setPageNumber} handleClose={closePageSelection}/>
        <Masonry
          breakpointCols={{default: 3, 1250: 2, 820:1}}
          className="my-masonry-grid"
          columnClassName="my-masonry-grid_column">
          {displayElements}
        </Masonry>
        <ReactPaginate
          forcePage = {pageNumber}
          previousLabel={"<<"}
          nextLabel={">>"}
          breakLabel={<div className="page-break-link" role="button" onClick={e => {
            e.stopPropagation();
            openPageSelection();  
          }}>...</div>}
          pageCount={Math.ceil(elements.length / elementsPerPage)}
          onPageChange={changePage}
          containerClassName={"pagination"}
          previousLinkClassName={"paginationPrevious"}
          nextClassName={"paginationNext"}
          disabledClassName={"disabled"}
          activeClassName={"page-item active"}
          activeLinkClassName={"page-link"}
          breakLinkClassName={"page-break"}
          pageLinkClassName={"page-link"}
          nextLinkClassName={"page-link"}
          previousClassName={"page-link"}
        />
      </div>
    );
  }